@media (max-width: 640px) { 

    #header {
        justify-content: center;
    }

    #header .header__name {
        margin: 0;
    }

    #header .header__nav {
        display: none !important;
    }

}

@media (max-width: 925px) { 

    #header .header__nav {
        width: 35%;
        margin: 0 var(--header-height);
    }

    #header .header__nav,
    #header .header__name {
        margin: 0;
        margin-left: 10px;
        margin-right: 10px;
    }

}