@media (max-width: 640px) { 

    #services {
        height: 100%;
        padding-top: 80px;
    }

    #services .services__container {
        padding-left: 7px;
        padding-right: 7px;
    }

    #services .services__card {
        width: 90vw;
    }

    #services .services__card .card__image {
        width: 20vw;
    }

}

@media (min-width: 641px) and (max-width: 970px) { 

    #services {
        height: 100%;
        padding-top: 80px;
    }

    #services .services__container {
        padding-left: 17px;
        padding-right: 17px;
    }

    #services .services__card {
        width: 36vw;
    }

    #services .services__card .card__image {
        width: 10vw;
    }
    
    #services .services__card .card__text { 
        font-size: .7rem;;
    }
}

@media (min-width: 1400px) { 

    #services { 
        justify-content: center;
    }

}