@media (max-width: 640px) { 

    #about {
        height: 100%;
        justify-content: start;
    }

    #about .about__container {
        padding-left: 7px;
        padding-right: 7px;
    }

    #about .about__content {
        flex-direction: column;
        align-items: center;
    }

    #about .about__image {
        width: 42vw;
        margin-bottom: 10px;
    }

    #about .about__image .back {
        display: none;
    }

    #about .about__text {
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #about .about__text .title {
        font-size: 2rem;
    }

    #about .about__text .text {
        font-size: .9rem;
        text-indent: 1rem;
        text-align: justify;
    }

    #about .about__text .bio {
        display: none;
    }

}

@media (min-width: 641px) and (max-width: 970px) { 

    #about {
        height: 100%;
        padding-top: 80px;
        justify-content: start;
    }

    #about .about__container {
        padding-left: 17px;
        padding-right: 17px;
    }

    #about .about__content {
        flex-direction: column;
        align-items: center;
    }

    #about .about__image {
        width: 25vw;
        margin-bottom: 20px;
    }

    #about .about__text {
        padding: 0;
        display: flex;
        flex-direction: column;
    }

    #about .about__text .title {
        font-size: 1.8rem;
    }

    #about .about__text .text {
        font-size: .9rem;
        text-align: justify;
    }

    #about .about__text .bio__text {
        font-size: .9rem;
    }

}

@media (min-width: 970px) and (max-width: 1170px) { 

    #about .about__image {
        width: 21vw;
        margin-bottom: 20px;
    }

    #about .about__image .back {
        display: none;
    }

}