#header {
    top: 0;
    z-index: 9;
    width: 100vw;
    display: flex;
    position: fixed;
    align-items: center;
    height: var(--header-height);
    justify-content: space-between;
    background-color: var(--header);
    transition: background-color 1s;
}

#header.header__top {
    transition: background-color 1s;
    background-color: var(--header-opacity);
}

#header .header__name {
    font-size: 1.6rem;
    font-weight: bold;
    color: var(--header-text);
    margin: 0 var(--header-height);
    font-family: 'Noto Sans JP', sans-serif;
}

#header .header__nav {
    width: 25%;
    margin: 0 var(--header-height);
}

#header .header__list {
    display: flex;
    list-style: none;
    font-size: 1.2rem;
    height: var(--header-height);
    justify-content: space-around;
}

#header .header__item {
    width: 100%;
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    height: var(--header-height);
}

#header .header__item:hover {
    transition: border-bottom 200ms;
    height: calc(var(--header-height) - 1);
    border-bottom: 1px solid var(--header-item-border);
}

#header .header__item:hover > .header__link {
    transition: color 200ms;
    color: var(--header-item-border);
}

#header .header__link {
    text-decoration: none;
    color: var(--header-text);
    font-family: 'Noto Sans JP', sans-serif;
}