@keyframes blinker {
    from { opacity: 1.0; }
    to { opacity: 0.0; }
}

#home {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background-color: var(--home);
}

#home .home__profile {
    width: 250px;
    height: 250px;
    padding: 5px;
    border-radius: 100%;
    background-color: white;
    transition: transform 500ms, box-shadow 900ms, width 200ms, height 200ms;;
}

#home .home__profile:hover {
    cursor: pointer;
    transform: scale(1.1);
    box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.5);
    transition: transform 1s,  box-shadow 900ms;
}

#home .home__image {
    width: 100%;
    height: 100%;
    border-radius: 100%;
}

#home .home__title {
    display: flex;
    align-items: center;
    flex-direction: column;
}

#home .home__name {
    margin-top: 20px;
    font-size: 2.5rem;
    color: var(--home-text);
    font-family: 'Noto Sans JP', sans-serif;
}

#home .home__work {
    display: flex;
    font-size: 1.5rem;
    align-items: center;
    color: var(--home-text-1);
    font-family: 'Noto Sans JP', sans-serif;
}

#home .home__cursor {
    float: right;
    width: 3px;
    height: 30px;
    margin-left: 5px;
    background-color: var(--home-text-1);
    animation: blinker 0.8s linear infinite;
}

#home .home__links {
    margin-top: 15px;
}

#home .home__links .image {
    margin: 5px;
    width: 28px;
    opacity: .6;
    border-radius: 4px;
    border: 1px solid var(--home-text-1);
}

#home .home__links .image:hover {
    opacity: 1;
    border: 1px solid var(--home-text);
    transition: opacity 500ms, border 500ms;
}