:root {
    --header-height: 70px;
    --header: rgb(25, 24, 25);
    --header-text: #a5a5a5;
    --header-opacity: rgba(25, 24, 25, .8);
    --header-item-border: white;
    --home: #25262A;
    --home-text: white;
    --home-text-1: #a5a5a5;
}

::-webkit-scrollbar {
    width: 10px;
    background-color: #a5a5a5;
}

::-webkit-scrollbar-track {
    background-color: #a5a5a5; 
}

::-webkit-scrollbar-thumb {
    background-color: #191819;
}

html {
    scroll-behavior: smooth;
}

html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

p, h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
}

[data-anime] {
    opacity: 0;
    transition: 300ms;
}

[data-anime="left"] {
    transform: translate3d(-60px, 0, 0);
}

[data-anime="right"] {
    transform: translate3d(60px, 0, 0);
}

[data-anime].animate {
    opacity: 1;
    transform: translate3d(0, 0, 0);
}