#about {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #25262A;
}

#about .about__container {
    padding-left: 70px;
    padding-right: 70px;
}

#about .about__title {
    color: #4d4d4d;
    font-size: 2.5rem;
    font-family: 'Noto Sans JP', sans-serif;
}

#about .about__content {
    display: flex;
    margin-top: 18px;
}

#about .about__image {
    width: 18.5vw;
    position: relative;
}

#about .about__image .image {
    z-index: 8;
    width: 100%;
    opacity: 1;
    position: relative;
    border: 8px solid white;
    transition: transform 800ms, opacity 500ms;
}

#about .about__image .image:hover {
    cursor: pointer;
    transform: scale(1.04);
    transition: transform 800ms, opacity 500ms;
}

#about .about__image .back {
    top: 0;
    left: 0;
    width: 100%;
    height: 96%;
    position: absolute;
    transform: rotate(-6deg);
    background-color: #9A969B;
    border: 8px solid white;
}

#about .about__text {
    flex: 1;   
    padding-left: 80px;
    padding-right: 80px;
}

#about .about__text .title {
    color: #a5a5a5;
    font-size: 2.8rem;
    font-family: 'Noto Sans JP', sans-serif;
}

#about .about__text .text {
    margin-top: 20px;
    letter-spacing: 0;
    color: #808080;
    font-size: 1.1rem;
    font-family: 'Noto Sans JP', sans-serif;
}

#about .about__text .bio {
    width: 100%;
    display: flex;
    margin-top: 20px;
}

#about .about__text .bio__info {
    width: 50%;
    display: flex;
    flex-direction: column;
}   

#about .about__text .bio__text {
    color: #808080;
    font-size: 1rem;
    margin-bottom: 5px;
    font-family: 'Noto Sans JP', sans-serif;
}

#about .about__text .bio__text strong {
    color: #a5a5a5;
}

#about .about__text .button {
    margin-top: 30px;
    height: 35px;
    width: 150px;
    display: flex;
    cursor: pointer;
    overflow: hidden;
    font-size: .9em;
    color: #808080;
    position: relative;   
    align-items: center;
    text-decoration: none;
    justify-content: center;
    border: 2px solid #808080;
    background-color: transparent;
}

#about .about__text .button a {
    color: #808080;
    position: relative;  
    text-decoration: none;
    transition: all .35s ease-Out;
    font-family: 'Noto Sans JP', sans-serif;
}

#about .about__text .button #slide {
    bottom: 0;
    width: 100%;
    height: 100%;
    left: -200px;
    position: absolute;
    background: #808080;
    transition: all .35s ease-Out;
}

#about .about__text .button:hover #slide {
    left: 0;
}
  
#about .about__text .button:hover a {
    color: #25262A;
}