#services {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-color: #25262A;
}

#services .services__container {
    padding-left: 70px;
    padding-right: 70px;
}

#services .services__title {
    color: #4d4d4d;
    font-size: 2.5rem;
    font-family: 'Noto Sans JP', sans-serif;
}

#services .services__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

#services .services__card {
    height: 28vh;
    width: 25vw;
    margin: 18px;
    padding: 20px;
    display: flex;
    cursor: pointer;
    position: relative;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    background-color: #2f3035;
}

#services .services__card:before,
#services .services__card:after {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    content: "";
    position: absolute;
    transition: transform 0.3s ease-in-out;
}

#services .services__card:before {
    transform: scaleY(0);
    border-left: 1px solid #a5a5a5;
    border-right: 1px solid #a5a5a5;
}

#services .services__card:after {
    transform: scaleX(0);
    border-top: 1px solid #a5a5a5;
    border-bottom: 1px solid #a5a5a5;
}

#services .services__card:hover:before {
    transform: scaleY(1);
}

#services .services__card:hover:after {
    transform: scaleX(1);
}

#services .services__card .card__image {
    width: 8vw;
}

#services .services__card .card__text {
    font-size: .8rem;
    color: #a5a5a5;
    line-break: auto;
    text-align: justify;
    text-indent: 3rem;
    font-family: 'Noto Sans JP', sans-serif;
}